<template>
  <div id='game-state-container'>
    <b-card class='m-3'>
      <b-card-text>
        {{ description }}
      </b-card-text>
    </b-card>
  </div>
</template>


<script>
export default {
  data() {
    return {
      description: ''
    }
  },
  methods: {
    setWaitingToStart() {
      this.description = 'Waiting To Start...'
    },
    setPlayersTurn(playerName) {
      this.description = `${playerName}'s turn...`
    },
    setPickedCard(playerName, cardTitle, meaning) {
      this.description = `${playerName} picked ${cardTitle}: ${meaning}`
    },
    setGameEnded() {
      this.description = `The game has finished, everyone has selected all the available cards!`
    }
  }
}
</script>

