import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import config from '../config.json'

export default new Vuex.Store({
  state: {
    apiEndpoint: config.apiEndpoint,
    gameName: '',
    gameID: '',       // This is just temporary.
    playerName: '',
    players: [],
    currentPlayer: '',
    isHost: false,
    gameStarted: false,
    playerPicked: false,
    gameEnded: false
  },
  mutations: {
    setPlayerName (state, name) {
      state.playerName = name
    },
    setGameID (state, gameID) {
      state.gameID = gameID
    },
    setGameName(state, gameName) {
      state.gameName = gameName
    },
    setPlayers(state, players) {
      state.players = [...players]
    },
    setHost(state, isHost) {
      state.isHost = isHost
    },
    setGameStarted(state, gameStarted) {
      state.gameStarted = gameStarted
    },
    setCurrentPlayer(state, currentPlayer) {
      state.currentPlayer = currentPlayer
    },
    setPlayerPicked(state, hasPicked) {
      state.playerPicked = hasPicked
    },
    setGameEnded(state, hasEnded) {
      state.gameEnded = hasEnded
    }
  },
  actions: {
    /**
     * When the user joins / connects to a game, thisis 
     * called to set the initial game properties.
     */
    setGame({ commit }, { gameName, username, gameID, players }) {
      commit('setPlayerName', username)
      commit('setGameID', gameID)
      commit('setGameName', gameName)
      commit('setPlayers', players)
      commit('setGameStarted', false)
      commit('setPlayerPicked', false)
      commit('setGameEnded', false)
    },
    setHost({ commit }, isHost) {
      commit('setHost', isHost)
    },
    updatePlayers ({ commit }, players) {
      commit('setPlayers', players)
    },
    setGameStarted({ commit }, gameStarted) {
      commit('setGameStarted', gameStarted)
    },
    setCurrentPlayer({ commit }, currentPlayer) {
      commit('setCurrentPlayer', currentPlayer)
    },
    setPlayerPicked({ commit }, hasPicked) {
      commit('setPlayerPicked', hasPicked)
    },
    resetGameProperties({ commit }) {
      commit('setPlayerName', '')
      commit('setGameID', '')
      commit('setGameName', '')
      commit('setPlayers', [])
      commit('setGameStarted', false)
      commit('setPlayerPicked', false)
      commit('setGameEnded', false)
    },
    setGameEnded({ commit }, gameEnded) {
      commit('setGameEnded', gameEnded)
    }
  },
  modules: {
  }
})
