<template>
  <!-- This component contains the logic of passing the user details to the remote server. -->
  <div id="join-game-container">
    <b-container class='text-center'>
      <b-row class='m-2'>
        <b-col>
          <h1>Join Game</h1>
        </b-col>
      </b-row>
      <b-row class='m-2'>
        <b-col>
          <b-form-input v-model='username' placeholder='Player Name'></b-form-input>
        </b-col>
      </b-row>
      <b-row class='m-2'>
        <b-col>
          <b-form-input v-model='gameID' placeholder='Game ID'></b-form-input>
        </b-col>
      </b-row>
      <b-row class='m-5'>
        <b-col>
          <b-button @click='joinGame' block variant="success">Join Game</b-button>
        </b-col>
      </b-row>
      <b-row>
        <p>{{ errorMsg }}</p>
      </b-row>
    </b-container>
  </div>
</template>

<script>

import { mapActions } from 'vuex'


export default {
  data() {
    return {
      errorMsg: '',
      username: '',
      gameID: ''
    }
  },
  methods: {
    ...mapActions([
      'setGame',
      'setHost',
      'resetGameProperties'
    ]),
    joinGame() {
      if (this.username.trim() === '' || this.gameID.trim() === '') {
        this.errorMsg = 'Please enter a valid username and gameID'
        return
      }

      this.$socket.emit('joinGame', { gameID: this.gameID, 
          username: this.username })
    }
  },
  sockets: {
    gameJoined(info) {
      this.setGame(info)
      this.setHost(false)
      this.$router.push('/game')
      this.errorMsg = ''
    },
    invalidGame() {
      this.errorMsg = 'The game you entered does not exist!'
    },
    usernameTaken() {
      this.errorMsg = `I'm afraid the username has already been taken!`
    }
  },
  mounted() {
    // When the component dismounts leave the room......
    this.$socket.disconnect()
    this.$socket.connect()
    this.resetGameProperties()
  }
}
</script>

