<template>
  <div id="card-control">
    <b-row>
      <b-col class="text-center">
        <transition name='component-fade' mode='out-in'>
          <component v-bind:is='currentCardImg' v-bind:src='cardImg'></component>
        </transition>
      </b-col>
    </b-row>
    <b-row class='text-center'>
      <b-col>
        <b-button
          :disabled='disableButtons'
          @click='$emit("selected-card")' 
          variant='success'>
            Select
          </b-button>
      </b-col>
      <b-col>
        <b-button 
          :disabled='disableButtons' 
          variant='info'
          @click='firstImg = !firstImg'>
            Another card
          </b-button>
      </b-col>
    </b-row>
  </div>
</template>

<script>

import { mapState } from 'vuex'

// TODO: MAKE ANOTHER CARD PERFORM AN ANIMATION

export default {
  data() {
    return {
      cardImg: ``,
      firstImg: true,        // Used for animation purposes.
    }
  },
  computed: {
    ...mapState([
      'apiEndpoint',
      'playerPicked',
      'currentPlayer',
      'playerName',
      'gameStarted'
    ]),
    disableButtons() {
      return this.$store.state.gameEnded || this.$store.state.playerPicked || 
              this.$store.state.playerName !== this.$store.state.currentPlayer || 
              !this.$store.state.gameStarted
    },
    currentCardImg() {
      if (this.firstImg)
        return 'card-copy-1'

      return 'card-copy-2'
    }
  },
  mounted() {
    this.cardImg = `${this.apiEndpoint}/card/back.png`
  },
  methods: {
    setCardToBack() {
      this.firstImg = !this.firstImg
      this.cardImg = `${this.apiEndpoint}/card/back.png`
    },
    setCardImg(img) {
      this.cardImg = `${this.apiEndpoint}/card/${img}`
    },
    animateCard() {
      switch (this.currentCardImg) {
        case 'card-copy-1': 
          this.currentCardImg = 'v-img-2'
          break 
        case 'card-copy-2':
          this.currentCardImg = 'v-img-1'
          break 
      }
    }
  },
  components: {
    'card-copy-1': {    // Used to make the card flash.
      template: `<img id='card-img' alt='the current card' />` 
    },
    'card-copy-2': {
      template: `<img id='card-img' alt='the current card' />`
    }
  },
  emits: ['selected-card']
}
</script>

<style scoped>

#card-img {
  margin-left: auto;
  margin-right: auto;
  height: 50vh;
}

.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}

.component-fade-enter, .component-fade-leave-to {
  opacity: 0;
}

</style>