<template>
  <!-- Contains a form asking the user for their details and submits the game details to the remote server -->
  <div id="create-game-container">
    <b-container class='text-center'>
      <b-row class='m-2'>
        <b-col>
          <h1>Create Game</h1>
        </b-col>
      </b-row>
      <b-row class='m-2'>
        <b-col>
          <b-form-input v-model='playerName' placeholder='Player Name'></b-form-input>
        </b-col>
      </b-row>
      <b-row class='m-2'>
        <b-col>
          <b-form-input v-model='gameName' placeholder='Game Name'></b-form-input>
        </b-col>
      </b-row>
      <b-row class='m-5'>
        <b-col>
          <b-button @click='createGame' block variant='success'>Create Game</b-button>
        </b-col>
      </b-row>
      <b-row class='m-2'>
        <b-col>
          <p :style='{ display: (isError) ? "inline-block" : "none" }'>Please enter a valid username and game name!</p>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>

import { mapActions } from 'vuex'

export default {
  data (){
    return {
      playerName: '',
      gameName: '',
      isError: false
    }
  },
  sockets: {
    gameJoined: function (info) {
      console.log('game is created');
      this.setGame(info)
      this.setHost(true)
      
      this.$router.push('/game')
    }
  },
  methods: {
    createGame() {
      if (this.playerName.trim() === '' || this.gameName.trim() === '') {
        this.isError = true
        return
      }

      this.isError = false
      this.$socket.emit('createGame', {gameName: this.gameName, hostName: this.playerName})
    },
    ...mapActions([
      'setGame',
      'setHost',
      'resetGameProperties'
    ])
  },
  mounted() {
    // When the component dismounts leave the room......
    this.$socket.disconnect()
    this.$socket.connect()
    this.resetGameProperties()
  }
}
</script>

