<template>
    <div class='player-container' :style='{border}'>
      <b-card class='player-element text-center' >
        <b-card-body>
          <p>{{ this.player }}</p>
        </b-card-body>
      </b-card>
    </div>
</template>

<script>

import { mapActions } from 'vuex'

export default {
  props: {
    player: String,
    isCurrentPlayer: Boolean
  },
  computed: {
    border() {
      return (this.isCurrentPlayer) ? '2px solid green' : '2px none black'
    },
  },
  methods: {
    ...mapActions([
      'updatePlayers'
    ])
  },
  sockets: {
    userJoined: function({ players }) {
      this.updatePlayers(players)
    }
  }
}
</script>

<style scoped>

.player-container {
  flex-grow: 1;
}

</style>