<template>
  <!-- This is the actual game view. Presents the user with a chat, controls
       to display their card and to end the round etc.... -->
  <div id="game-container" class="h-100">

    <b-container v-if='hasConnectedToGame' class="m-0 p-0 h-100 p-2" fluid> 
      <b-row>
        <b-col class="text-center">
          <h1>{{ gameName }}</h1>
          <h2>{{ gameID }}</h2>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <b-row>
            <b-col cols="3" class='mb-5'>
              <ChatBox
                ref='chatBoxComponent'>

              </ChatBox>
            </b-col>
            <b-col cols="9" class='text-center'>
              <CardControl
                ref='cardControlComponent'
                @selected-card='selectCard'>

              </CardControl>

              <GameState
                ref='gameStateComponent'>
              </GameState>

              <div v-if='$store.state.isHost && !$store.state.gameStarted'>
                <b-button 
                  variant='danger'
                  @click='startGame'>
                  Start Game
                </b-button>
              </div>
              <div v-else-if='showNextRoundButton'>
                <b-button
                  :disabled='gameEnded'
                  variant='danger'
                  @click='nextRound'>
                
                    Next Round
                
                </b-button>
              </div>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <div id='players-container' class='m-1'>
            <Player
              v-for='(player, index) in players' :key='index'
              :player='player'
              :isCurrentPlayer='player === currentPlayer'
            >
            </Player>
          </div>
        </b-col>
        
      </b-row>
    </b-container>
    <b-container v-else class='text-center'>
      <b-row>
        <b-col>
          <h1>You Have Not Filled In The Game Details!</h1>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <router-link to='/create'>
            <b-button variant='success' fluid>Create Game</b-button>
          </router-link>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import ChatBox from '../components/game/ChatBox'
import CardControl from '../components/game/CardControl'
import ActionControls from '../components/game/ActionControls'
import Player from '../components/game/Player'
import GameState from '../components/game/GameState'

import { mapState, mapActions } from 'vuex'

export default {
  computed: {
    ...mapState([
      'gameName',
      'gameID',
      'playerName',
      'players',
      'currentPlayer',
      'isHost',
      'gameStarted',
      'playerPicked',
      'gameEnded'
    ]),
    showNextRoundButton() {
      return this.playerName === this.currentPlayer && this.playerPicked
    },
    // Returns true if they have successfully connected to the game.
    // and the details have been filled in.
    hasConnectedToGame() {
      return (this.gameName.trim() && this.gameID.trim() 
            && this.playerName.trim() && this.players)
    }
  },
  components: {
    ChatBox,
    CardControl,
    ActionControls,
    Player,
    GameState
  },
  methods: {
    ...mapActions([
      'setGameStarted',
      'setCurrentPlayer',
      'setPlayerPicked',
      'setHost',
      'updatePlayers',
      'setGameEnded'
    ]),
    startGame() {
      this.$socket.emit('startGame')
    },
    selectCard() {
      if (this.currentPlayer === this.playerName) {
        this.$socket.emit('getCard')
      }
    },
    nextRound() {
      if (this.currentPlayer === this.playerName) {
        this.$socket.emit('nextRound')
      }                 
    }
  },
  sockets: {
    nextRound(playerTurn) {
      this.setGameStarted(true)
      this.$refs.gameStateComponent.setPlayersTurn(playerTurn)
      this.$refs.cardControlComponent.setCardToBack()
      this.setCurrentPlayer(playerTurn)
      this.setPlayerPicked(false)
    },
    pickedNextCard({ picker, card }) {
      this.$refs.cardControlComponent.setCardImg(card.href)
      this.$refs.gameStateComponent.setPickedCard(picker, card.title, card.action)
      this.setPlayerPicked(true)
    },
    userLeft({ players, host }) {
      this.setHost(host == this.playerName)
      this.updatePlayers(players)
    },
    gameEnded() {
      this.setGameEnded(true)
      this.$refs.gameStateComponent.setGameEnded()
    }
  },

  mounted() {
    this.$refs.gameStateComponent.setWaitingToStart()
  }
}
</script>

<style scoped>

#players-container {
  display: flex;
}

</style>