<template>
  <!-- The compnent is used to show a dialogue to the user when they try to submit a deck. -->
  <div id="deck-submitted-container" :style='{display, border}'>
    <div>
      <h1 class="text-center">{{message}}</h1>
    </div>
  </div>
</template>


<script>
export default {
  data () {
    return {
      display: 'none',
      border: '',
      message: ''
    }
  },
  methods: {
    setSubmitDialogue(isSubmit) {
     this.display = (isSubmit) ? 'block' : 'none'
     this.border = (isSubmit) ? '2px solid green' : ''
     this.message = 'Deck Successfully Added'
    },
    setErrorDialogue(isError) {
      this.display = (isError) ? 'block': none
      this.border = (isError) ? '2px solid red' : ''
      this.message = 'You did not enter values for all forms'
    }
  }
}
</script>


