<template>
  <div id="chatbox-container" class="h-100">
    <div id="chatbox" style="overflow-y: scroll;">
      <p v-for='(msg, index) in messages' :key='index'>
        <b>{{ msg.sender }}</b>: {{ msg.payload }}
      </p>
    </div>
    <div id="chat-input">
      <b-row class="m-0 p-0">
        <b-col cols="10" class="m-0 p-0">
          <b-form-input @keyup.enter.native='sendMessage' v-model='messageToSend' placeholder="Enter Chat Message" ></b-form-input>
        </b-col>
        <b-col cols="2" class="m-0 p-0">
          <b-button @click='sendMessage'>Enter</b-button> 
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      messageToSend: '',
      messages: []
    }
  },
  methods: {
    sendMessage() {
      this.$socket.emit('message', this.messageToSend.trim())
      this.messageToSend = ''
    }
  },
  sockets: {
    message({sender, payload}) {
      this.messages.push({sender, payload})
    }
  },
  emits: ['send-message']
}
</script>

<style scoped>

#chatbox {
  overflow-y: scroll;
  height: 70vh;
  overflow-wrap: break-word;
}

#chat-input {
  width: 100%;
}

</style>