<template>
  <!-- This component allows the user to modify the individual meaning of a card -->
  <div class='edit-card-container' :style="{width, border}">
    <div>
      <b-card :title='cardName'>
        <div>
          <b-img :src='cardSrc' class='img-thumbnail'></b-img>
        </div>

        <b-form-textarea
          v-model='meaning'
          placeholder='Enter a definition here'
          rows='3'
          max-rows='6'
        ></b-form-textarea>
      </b-card>
    </div>
  </div>
</template>

<script>
export default {
  /*
   * We pass the name of the card and 
   * the image source of the card as props.
   */
  props: {
    cardSrc: String,
    cardName: String,
  },

  data () {
    return {
      meaning: '',
      width: '250px',
      border: '2px none black'
    }
  },

  watch: {
    /*
     * Every time we change the definition of
     * the card send the modify card event to
     * the parent component. 
     */
    meaning: function (val) {
      this.$emit('modify-card', this.cardName, val);
    }
  },

  methods: {
    /*
     * Clear the action to be performed by the card.
     */
    clear() {
      this.meaning = "";
    },
    /*
     * Highlight the border red when called.
     */
    showError(isError) {
      this.border =  (isError) ? '2px solid red' : '2px none black'
    }
  },
  
  emits: ['modify-card']
}
</script>

<style scoped>

img {
  width: 173px;
  height: 264px;
}

</style>
